@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Stencil+Text:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
    font-size: 16px;
}
.header-container{
    display: flex;
    align-content: center;
    margin-top: 20px;
    height: 30%;
}
.logo-div{
    width: 40%;
}
#nameLogo{
    font-size:16px;
    font-weight: 900;
}
.menu-div {
    width: 60%;
    margin-right: 20px;
}
.menu-div ul {
    display: flex;
    list-style: none;
    justify-content: space-evenly;
    cursor: pointer;

}
.menu-div a{
    text-decoration: none;
    color:black;
    /* transition: all 0.3s ease; */
}
.menu-div a:hover{
    font-size: 17px;
    font-weight: 800;
}
.menu-btn{
    cursor: pointer;
}
.menu-img{
    display: none;
    height: 50px;
    width: 50px;
}

@media (max-width: 940px){
    .logo-div{
        width: 20%;
    }
    .menu-div {
        width: 80%;
    }
    .menu-div ul{
        justify-content: space-between;
    }
}
*{
    margin: 0;
    padding: 0;
}
.container{
    margin-top: 10px;
}
.About-title{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Big Shoulders Stencil Text', cursive;
