* {
  margin: 0;
  padding: 0;
  /* background-color: black; */
}
.body-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70%;
  margin-top: 120px;
  margin-bottom: 50px;
  
}
.about-left-container {
  margin: 0 auto;
}
#handGesture {
  display: inline-flex;
  font-size: 60px;
  -webkit-animation: handRotate 2s 4 alternate ease-in-out;
          animation: handRotate 2s 4 alternate ease-in-out;
}
#handGesture:hover {
  -webkit-animation: handRotate 2s 4 alternate ease-in-out;
          animation: handRotate 2s 4 alternate ease-in-out;
}

@-webkit-keyframes handRotate {
  0% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
  100% {
    -webkit-transform: rotate(12deg);
            transform: rotate(12deg);
  }
}

@keyframes handRotate {
  0% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
  100% {
    -webkit-transform: rotate(12deg);
            transform: rotate(12deg);
  }
}
#myName {
  font-size: 40px;
}
#litIntro {
  display: inline;
  font-size: 18px;
  letter-spacing: 2px;
}
#flag {
  font-size: 24px;
  margin-left: 2px;
}
#flag:hover {
  font-size: 27px;
}
#Resume {
  margin-top: 20px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  border-radius: 50px;
  text-decoration: none;
  background-color: #7595ec;
  color: black;
  height: 35px;
  width: 120px;
  font-weight: 550;
}
.resumeImg{
    height: 25px;
    width: 25px;
}
.social-icons {
  margin: 20px 0;
}
.social-icons ul {
  display: flex;
  list-style: none;
}
.social-icons ul li {
  margin: auto 7px;
}

@media (max-width: 940px) {
  .body-container {
    flex-direction: column-reverse;
    
  }
  .svgImg {
    height: 350px;
    width: 290px;
  }
  #myName {
    font-size: 28px;
  }
  #litIntro {
    font-size: 15px;
  }
  #handGesture {
    font-size: 40px;
  }
}


.svg-right-container{
    margin: 0 auto;
}
.svgImg #RightHand{
    -webkit-animation: rightHandAnime 2s ease-in-out infinite alternate;
            animation: rightHandAnime 2s ease-in-out infinite alternate;
    -webkit-transform-origin:right;
            transform-origin:right;
    transform-box:fill-box;
}
@-webkit-keyframes rightHandAnime {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(-2deg);
                transform: rotate(-2deg);
    }
}
@keyframes rightHandAnime {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(-2deg);
                transform: rotate(-2deg);
    }
}
.svgImg #LeftHand{
    -webkit-animation: leftHandAnime 2s ease-in-out infinite alternate;
            animation: leftHandAnime 2s ease-in-out infinite alternate;
    /* transform: rotate(9deg); */
    -webkit-transform-origin:left;
            transform-origin:left;
    transform-box:fill-box;
}
@-webkit-keyframes leftHandAnime {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
}
@keyframes leftHandAnime {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
}
.svgImg #Laptop {
    -webkit-animation: laptopAnime 2s ease-in-out infinite alternate;
            animation: laptopAnime 2s ease-in-out infinite alternate;
}
.svgImg #Laptop #path128, #path130{
    /* fill: #7595EC; */
    -webkit-animation: shapeChangeColor 5s infinite alternate;
            animation: shapeChangeColor 5s infinite alternate;
}
.svgImg #path124{
    -webkit-animation: laptopBackColor 3s infinite ease-in-out alternate;
            animation: laptopBackColor 3s infinite ease-in-out alternate;
}
.svgImg #path122{
    -webkit-animation: laptopKeyColor 3s infinite ease-in-out alternate;
            animation: laptopKeyColor 3s infinite ease-in-out alternate;
}
@-webkit-keyframes laptopBackColor {
    0%{
        fill: rgb(17, 160, 160);
    }
    100%{
        fill: rgb(95, 228, 233);
    }
}
@keyframes laptopBackColor {
    0%{
        fill: rgb(17, 160, 160);
    }
    100%{
        fill: rgb(95, 228, 233);
    }
}

@-webkit-keyframes laptopKeyColor {
    0%{
        fill: rgb(0, 167, 8);
        opacity: 0.2;
    }
    100%{
        fill: rgb(241, 41, 41);
    }
}

@keyframes laptopKeyColor {
    0%{
        fill: rgb(0, 167, 8);
        opacity: 0.2;
    }
    100%{
        fill: rgb(241, 41, 41);
    }
}
@-webkit-keyframes laptopAnime {
    0%{
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
    100%{
        -webkit-transform: translateY(-35px);
                transform: translateY(-35px);
    }
}
@keyframes laptopAnime {
    0%{
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
    100%{
        -webkit-transform: translateY(-35px);
                transform: translateY(-35px);
    }
}
.svgImg #Hair{
    -webkit-animation: hairAnime 2s infinite alternate ease-in-out;
            animation: hairAnime 2s infinite alternate ease-in-out;
}
@-webkit-keyframes hairAnime {
    0%{
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
    100%{
        -webkit-transform: translateY(-3px);
