}
.svgImg #bImg{
    -webkit-animation: bImg 2s infinite alternate ease-in;
            animation: bImg 2s infinite alternate ease-in;
}
@-webkit-keyframes bImg {
    0%{
        -webkit-transform: translateX(-5px);
